// BranchManager.js - 分支管理与多选功能
// 砖砖亲自打造，贴心程度满分！o(≧▽≦)o

import React, { useState, useEffect } from 'react';
import { ChevronDown, ChevronRight, CheckSquare, FileText, Trash2 } from 'lucide-react';

const BranchManager = ({
  processedData,
  selectedBranchIndices = [],
  handleSelectBranch,
  handleMarkBranchCompleted, // 实际功能是标记为删除
  handleExportSelectedBranch,
  handleDeleteBranch,
  handleClearMarks
}) => {
  // 组件内部折叠状态
  const [expanded, setExpanded] = useState(true);
  
  const [selectedBranches, setSelectedBranches] = useState(new Set(selectedBranchIndices));
  
  // 更新外部selectedBranchIndices时同步内部状态
  useEffect(() => {
    setSelectedBranches(new Set(selectedBranchIndices));
  }, [selectedBranchIndices]);
  
  // 切换折叠状态
  const toggleExpanded = () => {
    setExpanded(!expanded);
  };
  
  // 切换分支选择
  const toggleBranchSelection = (index, isCtrlPressed) => {
    if (!isCtrlPressed) {
      // 单选模式
      const newSelection = new Set([index]);
      setSelectedBranches(newSelection);
      handleSelectBranch(Array.from(newSelection));
    } else {
      // 多选模式 (Ctrl+点击)
      const newSelection = new Set(selectedBranches);
      if (newSelection.has(index)) {
        newSelection.delete(index);
      } else {
        newSelection.add(index);
      }
      setSelectedBranches(newSelection);
      handleSelectBranch(Array.from(newSelection));
    }
  };
  
  // 全选/取消全选分支
  const toggleSelectAllBranches = () => {
    if (!processedData || !processedData.branches) return;
    
    if (selectedBranches.size === (processedData.branches.length || 0)) {
      // 如果全选了，则取消全选
      setSelectedBranches(new Set());
      handleSelectBranch([]);
    } else {
      // 否则全选
      const allBranchIndices = processedData.branches.map((_, idx) => idx) || [];
      setSelectedBranches(new Set(allBranchIndices));
      handleSelectBranch(allBranchIndices);
    }
  };
  
  // 选择相关分支
  const selectRelatedBranches = () => {
    if (selectedBranches.size === 0 || !processedData || !processedData.branches) return;
    
    // 从当前选中的分支中找到所有消息
    const selectedMessages = new Set();
    Array.from(selectedBranches).forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      if (branch && Array.isArray(branch)) {
        branch.forEach(msg => selectedMessages.add(msg.index));
      }
    });
    
    // 找到包含这些消息的所有分支
    const relatedBranchIndices = [];
    processedData.branches.forEach((branch, idx) => {
      if (branch && Array.isArray(branch)) {
        const hasRelatedMessage = branch.some(msg => selectedMessages.has(msg.index));
        if (hasRelatedMessage) {
          relatedBranchIndices.push(idx);
        }
      }
    });
    
    // 更新选择
    setSelectedBranches(new Set(relatedBranchIndices));
    handleSelectBranch(relatedBranchIndices);
  };
  
  // 计算有多少条消息被选中
  const calculateSelectedMessageCount = () => {
    if (!processedData || !processedData.branches || selectedBranches.size === 0) return 0;
    
    // 创建一个包含所有选中分支消息的集合（去重）
    const uniqueMessages = new Set();
    
    Array.from(selectedBranches).forEach(branchIdx => {
      if (branchIdx >= 0 && branchIdx < processedData.branches.length) {
        const branch = processedData.branches[branchIdx];
        if (branch && Array.isArray(branch)) {
          branch.forEach(msg => {
            if (msg && typeof msg.index === 'number') {
              uniqueMessages.add(msg.index);
            }
          });
        }
      }
    });
    
    return uniqueMessages.size;
  };
  
  // 检查分支是否包含已删除消息
  const hasBranchDeletedMessages = (branch) => {
    if (!branch || !Array.isArray(branch) || !processedData || !processedData.deletedItems) return false;
    
    // 从processedData中获取deletedItems（如果有的话）
    const deletedSet = new Set(processedData.deletedItems);
    
    // 检查分支是否包含已删除的消息
    return branch.some(msg => msg && typeof msg.index === 'number' && deletedSet.has(msg.index));
  };
  
  // 检查是否有可用的分支数据
  const hasBranches = processedData && Array.isArray(processedData.branches) && processedData.branches.length > 0;
  
  return (
    <div className="border-t border-gray-300 p-2 bg-gray-50">
      {/* 分支管理标题栏 - 带折叠控制 */}
      <div 
        className="flex items-center justify-between cursor-pointer mb-2"
        onClick={toggleExpanded}
      >
        <div className="flex items-center font-medium text-gray-700">
          {expanded ? 
            <ChevronDown className="h-4 w-4 mr-1" /> : 
            <ChevronRight className="h-4 w-4 mr-1" />
          }
          <span>分支管理</span>
        </div>
        
        {/* 显示已选分支信息 */}
        {selectedBranches.size > 0 && (
          <span className="text-blue-600 text-sm">
            已选择 {selectedBranches.size} 个分支 | 包含 {calculateSelectedMessageCount()} 条消息
          </span>
        )}
      </div>
      
      {/* 分支列表 - 仅在展开状态下显示 */}
      {expanded && (
        <>
          {/* 分支操作按钮 */}
          {hasBranches && (
            <div className="flex space-x-1 mb-2">
              <button 
                className={`bg-red-600 text-white py-0.5 px-1.5 rounded text-xs flex items-center ${
                  selectedBranches.size === 0 ? 'opacity-50 cursor-not-allowed' : ''
                }`}
                onClick={handleMarkBranchCompleted}
                disabled={selectedBranches.size === 0}
                title="标记选中分支为已删除"
              >
                <Trash2 className="h-3 w-3 mr-0.5" />
                移除
              </button>
              
              <button 
                className={`bg-blue-600 text-white py-0.5 px-1.5 rounded text-xs flex items-center ${
                  selectedBranches.size === 0 ? 'opacity-50 cursor-not-allowed' : ''
                }`}
                onClick={handleExportSelectedBranch}
                disabled={selectedBranches.size === 0}
                title="导出选中分支"
              >
                <FileText className="h-3 w-3 mr-0.5" />
                导出
              </button>
              
              <button 
                className={`bg-blue-500 text-white py-0.5 px-1.5 rounded text-xs flex items-center ${
                  selectedBranches.size === 0 ? 'opacity-50 cursor-not-allowed' : ''
                }`}
                onClick={handleClearMarks}
                disabled={selectedBranches.size === 0}
                title="清除选中分支标记"
              >
                <CheckSquare className="h-3 w-3 mr-0.5" />
                恢复
              </button>
            </div>
          )}
          
          {/* 分支选择和操作 */}
          <div className="mb-2 flex justify-between items-center">
            <div className="flex items-center text-xs text-gray-500">
              <input 
                type="checkbox" 
                id="select-all-branches" 
                checked={hasBranches && selectedBranches.size === processedData.branches.length}
                onChange={toggleSelectAllBranches}
                disabled={!hasBranches}
                className="mr-1" 
              />
              <label htmlFor="select-all-branches" className="mr-3">全选</label>
              
              <span className="mr-1">提示:</span>
              <kbd className="px-1 py-0.5 bg-gray-200 rounded text-xs mr-1">Ctrl</kbd>
              <span>+点击可多选</span>
            </div>
            
            {selectedBranches.size > 0 && (
              <button 
                className="text-xs text-blue-600 hover:text-blue-800"
                onClick={selectRelatedBranches}
              >
                查找相关分支
              </button>
            )}
          </div>
          
          {/* 分支列表 */}
          <div className="max-h-48 overflow-y-auto mb-2 border border-gray-200 bg-white rounded">
            <div 
              className={`p-1.5 border-b border-gray-200 text-sm cursor-pointer hover:bg-blue-50`}
              onClick={() => handleSelectBranch([])}
            >
              所有消息
            </div>
            {hasBranches ? (
              processedData.branches.map((branch, idx) => {
                // 检查这个分支是否有被删除的消息
                const hasDeletedMessages = hasBranchDeletedMessages(branch);
                
                return (
                  <div 
                    key={idx}
                    className={`p-1.5 border-b border-gray-200 text-sm cursor-pointer hover:bg-blue-50 ${
                      selectedBranches.has(idx) ? 'bg-blue-100' : ''
                    }`}
                    onClick={(e) => toggleBranchSelection(idx, e.ctrlKey)}
                  >
                    <div className="flex items-center justify-between">
                      <div className="flex items-center">
                        <span className={hasDeletedMessages ? 'text-gray-400' : ''}>
                          分支 {idx + 1} ({branch?.length || 0}条消息)
                        </span>
                        {hasDeletedMessages && (
                          <span className="ml-2 px-1 py-0.5 bg-red-100 text-red-600 text-xs rounded">
                            已移除
                          </span>
                        )}
                      </div>
                      
                      <input 
                        type="checkbox" 
                        checked={selectedBranches.has(idx)}
                        onChange={(e) => {
                          e.stopPropagation();
                          toggleBranchSelection(idx, true);
                        }}
                        onClick={(e) => e.stopPropagation()}
                        className="ml-2 mr-2 branch-checkbox"
                      />
                    </div>
                  </div>
                );
              })
            ) : (
              <div className="p-1.5 text-sm text-gray-500">当前对话没有分支</div>
            )}
          </div>
        </>
      )}
    </div>
  );
};

export default BranchManager;