import React, { useState } from 'react';
import { ChevronDown, ChevronRight } from 'lucide-react';

// 分支统计面板组件 - 移除了分支消息分布部分
const BranchStatsPanel = ({ processedData }) => {
  const [expanded, setExpanded] = useState(true);
  
  // 检查是否有可用的分支数据
  const hasBranches = processedData && Array.isArray(processedData.branches) && processedData.branches.length > 0;
  
  return (
    <div className="p-3 tab-content-container">
      <div className="bg-white p-4 rounded border border-gray-200 panel-content-area">
        <div 
          className="flex items-center justify-between cursor-pointer mb-3"
          onClick={() => setExpanded(!expanded)}
        >
          <div className="flex items-center font-medium text-gray-700">
            {expanded ? 
              <ChevronDown className="h-4 w-4 mr-1" /> : 
              <ChevronRight className="h-4 w-4 mr-1" />
            }
            <span>分支统计详情</span>
          </div>
        </div>
        
        {expanded && (
          hasBranches ? (
            <div className="grid grid-cols-2 gap-4">
              <div>
                <div className="text-gray-700 mb-1">总分支数:</div>
                <div className="font-medium text-blue-700 text-lg">{processedData.branches.length}</div>
              </div>
              <div>
                <div className="text-gray-700 mb-1">分支点数量:</div>
                <div className="font-medium text-blue-700 text-lg">
                  {processedData.chat_history?.filter(msg => msg.is_branch_point).length || 0}
                </div>
              </div>
              <div>
                <div className="text-gray-700 mb-1">最长分支:</div>
                <div className="font-medium text-blue-700 text-lg">
                  {processedData.branches.reduce((max, branch) => 
                    Math.max(max, Array.isArray(branch) ? branch.length : 0), 0)}条消息
                </div>
              </div>
              <div>
                <div className="text-gray-700 mb-1">最大分支深度:</div>
                <div className="font-medium text-blue-700 text-lg">
                  {processedData.chat_history?.reduce((max, msg) => 
                    Math.max(max, msg.branch_level || 0), 0) || 0}
                </div>
              </div>
            </div>
          ) : (
            <div className="text-gray-500 text-center py-6 bg-gray-50 rounded border border-gray-200">
              当前对话没有分支数据
            </div>
          )
        )}
        
        {expanded && hasBranches && (
          <div className="mt-4 p-3 bg-blue-50 rounded border border-blue-200 text-sm">
            <div className="font-medium text-blue-700 mb-1">提示：</div>
            <p>您可以在左侧分支管理部分选择特定分支以查看和导出其中的消息。分支选择后，相应的消息会在消息列表中被高亮显示。</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default BranchStatsPanel;