// 工具函数集合

// 解析时间戳
export const parseTimestamp = (timestampStr) => {
  if (!timestampStr) return "未知时间";
  
  try {
    // 处理带时区的时间戳
    if (timestampStr.includes("+")) {
      timestampStr = timestampStr.split("+")[0];
    } else if (timestampStr.includes("Z")) {
      timestampStr = timestampStr.replace("Z", "");
    }
    
    const dt = new Date(timestampStr);
    return dt.toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit'
    });
  } catch (error) {
    console.error("解析时间戳错误:", error);
    return "未知时间";
  }
};

// 从JSON数据中提取对话内容
export const extractChatData = (jsonData) => {
  // 提取元数据
  const title = jsonData.name || "无标题对话";
  const createdAt = parseTimestamp(jsonData.created_at);
  const updatedAt = parseTimestamp(jsonData.updated_at);

  const metaInfo = {
    title,
    created_at: createdAt,
    updated_at: updatedAt,
    project_uuid: jsonData.project_uuid || "",
    uuid: jsonData.uuid || ""
  };

  // 提取聊天历史
  const chatHistory = [];

  try {
    // 处理消息
    const messages = jsonData.chat_messages || [];

    messages.forEach((msg, msgIdx) => {
      const sender = msg.sender || "unknown";
      const senderLabel = sender === "human" ? "人类" : "Claude";
      const timestamp = parseTimestamp(msg.created_at);

      // 准备消息结构
      const messageData = {
        index: msgIdx,
        uuid: msg.uuid || "",
        parent_uuid: msg.parent_message_uuid || "",
        sender,
        sender_label: senderLabel,
        timestamp,
        content_items: [],
        raw_text: "",
        display_text: "",
        thinking: "",
        tools: [],
        artifacts: [],
        citations: [],
        // 分支相关字段
        branch_id: null,
        is_branch_point: false,
        branch_level: 0
      };

      // 处理消息内容
      if (msg.content && Array.isArray(msg.content)) {
        // 新格式: 使用content数组
        processContentArray(msg.content, messageData);
      } else if (msg.text) {
        // 旧格式: 直接使用text字段
        messageData.raw_text = msg.text;
        messageData.display_text = msg.text;
      }

      chatHistory.push(messageData);
    });
  } catch (error) {
    console.error("解析JSON时出错:", error);
    throw error;
  }

  return {
    meta_info: metaInfo,
    chat_history: chatHistory,
    raw_data: jsonData
  };
};

// 处理content数组中的所有元素
const processContentArray = (contentArray, messageData) => {
  let displayText = "";

  contentArray.forEach(item => {
    if (!item || typeof item !== 'object') return;
    
    const contentType = item.type || "";
    
    // 处理不同类型的内容
    if (contentType === "text") {
      const text = item.text || "";
      messageData.raw_text += text;
      displayText += text;
      
      // 处理citations字段
      if (item.citations && Array.isArray(item.citations)) {
        item.citations.forEach(citation => {
          if (citation && typeof citation === 'object') {
            messageData.citations.push(citation);
          }
        });
      }
    } 
    else if (contentType === "thinking") {
      const thinking = item.thinking || "";
      messageData.thinking = thinking.trim();
    }
    else if (contentType === "tool_use") {
      // 检测是否为artifacts工具
      if (item.name === "artifacts") {
        const artifactData = extractArtifact(item);
        if (artifactData) {
          messageData.artifacts.push(artifactData);
        }
      } else {
        // 处理其他工具
        const toolData = extractToolUse(item);
        if (toolData) {
          messageData.tools.push(toolData);
        }
      }
    }
    else if (contentType === "tool_result") {
      // 检测是否为artifacts工具结果
      if (item.name && item.name.includes("artifacts")) {
        // 将artifacts工具结果附加到最后一个artifacts
        if (messageData.artifacts.length > 0) {
          messageData.artifacts[messageData.artifacts.length - 1].result = 
            extractToolResult(item);
        }
      } else {
        // 处理其他工具结果
        const toolResult = extractToolResult(item);
        if (toolResult && messageData.tools.length > 0) {
          // 将结果添加到最近的工具使用记录
          messageData.tools[messageData.tools.length - 1].result = toolResult;
        }
      }
    }
  });
  
  // 更新消息数据
  messageData.content_items = contentArray;
  messageData.display_text = displayText.trim();
};

// 提取artifact信息
const extractArtifact = (artifactItem) => {
  try {
    const artifactInput = artifactItem.input || {};
    const command = artifactInput.command || "";
    const artifactId = artifactInput.id || "";
    const artifactType = artifactInput.type || "";
    const artifactTitle = artifactInput.title || "无标题";

    // 获取内容或更新信息
    if (command === "create") {
      const content = artifactInput.content || "";
      const artifactData = {
        id: artifactId,
        command,
        type: artifactType,
        title: artifactTitle,
        content,
        language: artifactInput.language || "",
        result: null
      };
      return artifactData;
    } else if (command === "update" || command === "rewrite") {
      const oldStr = artifactInput.old_str || "";
      const newStr = artifactInput.new_str || "";
      const artifactData = {
        id: artifactId,
        command,
        old_str: oldStr,
        new_str: newStr,
        result: null
      };
      return artifactData;
    }
  } catch (error) {
    console.error("提取artifact时出错:", error);
  }
  return null;
};

// 提取工具使用信息
const extractToolUse = (toolItem) => {
  const toolName = toolItem.name || "unknown";
  const toolInput = toolItem.input || {};

  const toolData = {
    name: toolName,
    input: toolInput,
    result: null
  };

  // 特别处理web_search
  if (toolName === "web_search" && typeof toolInput === 'object') {
    const query = toolInput.query || "";
    if (query) {
      toolData.query = query;
    }
  }

  return toolData;
};

// 提取工具结果信息
const extractToolResult = (resultItem) => {
  const toolName = resultItem.name || "unknown";
  const content = resultItem.content || [];
  const isError = resultItem.is_error || false;

  return {
    name: toolName,
    is_error: isError,
    content
  };
};

// 检测对话中的分支结构
export const detectBranches = (processedData) => {
  if (!processedData || !processedData.chat_history) {
    return processedData;
  }
  
  try {
    // 提取消息列表
    const messages = processedData.chat_history;
    
    // 构建消息字典(UUID -> 消息)
    const msgDict = {};
    const parentChildren = {}; // 父消息UUID -> 子消息UUID列表
    
    messages.forEach(msg => {
      const uuid = msg.uuid;
      const parentUuid = msg.parent_uuid;
      
      if (!uuid) return;
      
      msgDict[uuid] = msg;
      
      if (parentUuid) {
        if (!parentChildren[parentUuid]) {
          parentChildren[parentUuid] = [];
        }
        parentChildren[parentUuid].push(uuid);
      }
    });
    
    // 找到分支点（有多个子消息的消息）
    const branchPoints = [];
    
    Object.entries(parentChildren).forEach(([parentUuid, children]) => {
      if (children.length > 1) {
        branchPoints.push(parentUuid);
        if (msgDict[parentUuid]) {
          msgDict[parentUuid].is_branch_point = true;
        }
      }
    });
    
    // 为每个分支点找出其所有分支
    const branches = [];
    let branchId = 0;
    
    branchPoints.forEach(branchPoint => {
      const children = parentChildren[branchPoint] || [];
      
      children.forEach((childUuid, i) => {
        const branch = [];
        let currentUuid = childUuid;
        
        // 添加分支点消息
        if (branchPoint in msgDict) {
          branch.push(msgDict[branchPoint]);
        }
        
        // 跟踪这条分支链
        while (currentUuid && msgDict[currentUuid]) {
          const msg = msgDict[currentUuid];
          msg.branch_level = i + 1;
          branch.push(msg);
          
          // 找下一条消息
          const children = parentChildren[currentUuid] || [];
          if (children.length > 0) {
            currentUuid = children[0]; // 只跟踪第一个子消息
          } else {
            currentUuid = null;
          }
        }
        
        // 为这条分支分配ID
        branchId += 1;
        branch.forEach(msg => {
          msg.branch_id = branchId;
        });
        
        branches.push(branch);
      });
    });
    
    // 更新processedData
    return {
      ...processedData,
      branches,
      branch_points: branchPoints
    };
    
  } catch (error) {
    console.error("分支检测出错:", error);
    return {
      ...processedData,
      branches: [],
      branch_points: []
    };
  }
};

// 导出为Markdown
export const exportChatAsMarkdown = (processedData, config) => {
  const { meta_info, chat_history } = processedData;
  const lines = [];

  // 添加YAML前置元数据
  if (config.exportObsidianMetadata) {
    lines.push("---");
    lines.push(`title: ${meta_info.title || '对话记录'}`);
    lines.push(`date: ${new Date().toISOString().split('T')[0]}`);

    // 添加自定义属性
    if (config.obsidianProperties && config.obsidianProperties.length > 0) {
      config.obsidianProperties.forEach(prop => {
        const { name, value } = prop;
        
        // 处理列表类型的值
        if (value.includes(",") && !value.includes("[")) {
          const values = value.split(",").map(v => v.trim());
          lines.push(`${name}:`);
          values.forEach(v => {
            lines.push(`  - ${v}`);
          });
        } else {
          // 处理简单值
          lines.push(`${name}: ${value}`);
        }
      });
    }

    // 添加标签
    if (config.obsidianTags && config.obsidianTags.length > 0) {
      lines.push("tags:");
      config.obsidianTags.forEach(tag => {
        lines.push(`  - ${tag}`);
      });
    }

    lines.push("---");
    lines.push("");
  }

  // 添加标题和元信息
  lines.push(`# ${meta_info.title || '对话记录'}`);
  lines.push(`*创建时间: ${meta_info.created_at || '未知'}*`);
  lines.push(`*导出时间: ${new Date().toLocaleString('zh-CN')}*`);

  lines.push("");
  lines.push("---");
  lines.push("");

  // 添加消息内容
  chat_history.forEach(msg => {
    // 如果设置为只导出已标记并且该消息未被标记，则跳过
    if (config.exportMarkedOnly && !config.markedItems.has(msg.index)) {
      return;
    }

    const sender = msg.sender_label;
    const timestamp = msg.timestamp;
    const msgIndex = msg.index + 1; // 序号从1开始

    // 添加分支标记
    let branchMarker = "";
    if (msg.is_branch_point) {
      branchMarker = " 🔀";
    } else if (msg.branch_level > 0) {
      branchMarker = ` ↳${msg.branch_level}`;
    }

    lines.push(`## ${msgIndex}. ${sender}${branchMarker}`);

    // 根据配置决定是否显示时间戳
    if (!config.hideTimestamps) {
      lines.push(`*${timestamp}*`);
    }

    lines.push("");

    // 添加正文内容
    if (msg.display_text) {
      lines.push(msg.display_text);
      lines.push("");
    }

    // 添加思考过程
    if (msg.thinking && config.includeThinking) {
      lines.push("<details>");
      lines.push("<summary>思考过程</summary>");
      lines.push("");
      lines.push("```");
      lines.push(msg.thinking);
      lines.push("```");
      lines.push("</details>");
      lines.push("");
    }

    // 添加artifacts内容
    if (msg.artifacts && msg.artifacts.length > 0 && config.includeArtifacts) {
      msg.artifacts.forEach(artifact => {
        const artifactId = artifact.id || '未知';
        const artifactType = artifact.type || '未知';
        const artifactTitle = artifact.title || '无标题';

        lines.push("<details>");
        lines.push(`<summary>Artifact: ${artifactTitle} (ID: ${artifactId})</summary>`);
        lines.push("");

        // 显示类型
        lines.push(`**类型**: \`${artifactType}\``);
        lines.push("");

        const command = artifact.command || '';
        if (command === 'create') {
          const content = artifact.content || '';
          const language = artifact.language || '';

          if (language) {
            lines.push(`**语言**: \`${language}\``);
            lines.push("");
            lines.push("**内容**:");
            lines.push(`\`\`\`${language}`);
            lines.push(content);
            lines.push("```");
          } else {
            lines.push("**内容**:");
            lines.push("```");
            lines.push(content);
            lines.push("```");
          }
        } else if (command === 'update' || command === 'rewrite') {
          const oldStr = artifact.old_str || '';
          const newStr = artifact.new_str || '';

          lines.push(`**操作**: \`${command}\``);
          lines.push("");
          lines.push("**原始文本**:");
          lines.push("```");
          lines.push(oldStr);
          lines.push("```");
          lines.push("");
          lines.push("**新文本**:");
          lines.push("```");
          lines.push(newStr);
          lines.push("```");
        }

        lines.push("</details>");
        lines.push("");
      });
    }

    // 添加工具使用记录
    if (msg.tools && msg.tools.length > 0 && config.includeTools) {
      msg.tools.forEach(tool => {
        const toolName = tool.name;

        lines.push("<details>");
        lines.push(`<summary>工具: ${toolName}</summary>`);
        lines.push("");

        // 查询内容
        if (toolName === "web_search" && tool.query) {
          lines.push(`**搜索查询**: \`${tool.query}\``);
          lines.push("");
        }

        // 工具输入
        if (tool.input && typeof tool.input === 'object') {
          lines.push("**输入参数**:");
          lines.push("```json");
          try {
            lines.push(JSON.stringify(tool.input, null, 2));
          } catch (error) {
            lines.push(String(tool.input));
          }
          lines.push("```");
          lines.push("");
        }

        // 工具结果
        if (tool.result) {
          lines.push("**结果**:");

          if (tool.result.is_error) {
            lines.push("> ⚠️ 工具执行出错");
            lines.push("");
          }

          // Web搜索结果特殊处理
          if (toolName === "web_search") {
            const resultContent = tool.result.content || [];
            const maxResults = Math.min(resultContent.length, 5); // 最多显示5个结果

            if (resultContent.length > 0) {
              lines.push("搜索结果:");
              lines.push("");

              for (let i = 0; i < maxResults; i++) {
                const item = resultContent[i];
                if (!item || typeof item !== 'object') continue;

                const title = item.title || "无标题";
                const url = item.url || "#";

                lines.push(`${i + 1}. [${title}](${url})`);
              }

              if (resultContent.length > maxResults) {
                lines.push(`*...还有 ${resultContent.length - maxResults} 个结果未显示*`);
              }

              lines.push("");
            }
          }
        }

        lines.push("</details>");
        lines.push("");
      });
    }

    // 添加引用
    if (msg.citations && msg.citations.length > 0 && config.includeCitations) {
      lines.push("<details>");
      lines.push("<summary>引用来源</summary>");
      lines.push("");

      lines.push("| 标题 | 来源 |");
      lines.push("| --- | --- |");

      msg.citations.forEach(citation => {
        const title = citation.title || "未知来源";
        const url = citation.url || "#";
        const source = url.includes('/') ? url.split('/')[2] : '未知网站';
        lines.push(`| [${title}](${url}) | ${source} |`);
      });

      lines.push("</details>");
      lines.push("");
    }

    lines.push("---");
    lines.push("");
  });

  return lines.join("\n");
};

// 保存文本到文件
export const saveTextFile = (text, fileName) => {
  const blob = new Blob([text], { type: 'text/markdown' });
  const url = URL.createObjectURL(blob);
  const a = document.createElement('a');
  a.href = url;
  a.download = fileName;
  document.body.appendChild(a);
  a.click();
  document.body.removeChild(a);
  URL.revokeObjectURL(url);
};