import React from 'react';
import { Search, Trash2, Loader } from 'lucide-react';

const MessageList = ({ 
  messages, 
  selectedIndices, 
  onMessageSelect, 
  markedItems,
  importantItems,
  deletedItems,
  searchQuery,
  setSearchQuery,
  handleSearchMessages,
  showCompleted,
  showDeleted,
  handleMarkDeleted,
  isSearching,
  showSearchPanel // 添加打开全局搜索面板的函数
}) => {
  // 处理消息点击事件，确保在任何情况下都能正常工作
  const handleMessageClick = (index, ctrlKey) => {
    // 先停止事件冒泡，防止其他监听器干扰点击行为
    if (typeof onMessageSelect === 'function') {
      // 使用异步调用确保事件处理完成，防止React事件系统中的问题
      setTimeout(() => {
        onMessageSelect(index, ctrlKey);
      }, 0);
    }
  };

  // 处理搜索提交
  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      handleSearchMessages();
    }
  };
  
  // 处理搜索输入框按回车
  const handleSearchKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      if (searchQuery.trim()) {
        handleSearchMessages();
      }
    }
  };
  
  // 切换到全局搜索面板
  const openGlobalSearchPanel = () => {
    if (typeof showSearchPanel === 'function') {
      showSearchPanel();
    }
  };

  return (
    <div className="flex flex-col h-full overflow-hidden">
      {/* 顶部搜索区域 - 修改后的搜索UI */}
      <div className="p-2 border-b border-gray-300 bg-white">
        <div className="flex space-x-2 items-center">
          <div className="relative flex-1">
            <input 
              type="text" 
              placeholder="搜索全部消息..." 
              className="w-full pl-8 pr-2 py-1 border border-gray-300 rounded text-sm"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyPress={handleSearchKeyPress}
            />
            <Search
              className="absolute left-2 top-1.5 h-4 w-4 text-gray-400 cursor-pointer" 
              onClick={openGlobalSearchPanel}
              title="打开全局搜索面板"
            />
            {isSearching && (
              <Loader className="absolute right-2 top-1.5 h-4 w-4 text-blue-500 animate-spin" />
            )}
          </div>
          <button 
            className="bg-blue-500 text-white py-1 px-2 rounded text-sm whitespace-nowrap hover:bg-blue-600 flex items-center"
            onClick={handleSearchMessages}
            title="跨文件向下搜索"
          >
            <Search className="h-3.5 w-3.5 mr-1" />
            搜索
          </button>
        </div>
      </div>
      
      {/* 消息列表表头 */}
      <div className="bg-gray-100 border-b border-gray-300 p-1 sticky top-0 z-10">
        <div className="grid grid-cols-12 text-sm font-medium text-gray-600">
          <div className="col-span-1 px-3">#</div>
          <div className="col-span-2 px-1">发送者</div>
          <div className="col-span-2 px-1">时间</div>
          <div className="col-span-2 px-1">分支</div>
          <div className="col-span-2 px-1">标记</div>
          <div className="col-span-2 px-1 flex justify-end pr-2">
            <button
              className="text-gray-500 hover:text-red-600 focus:outline-none"
              onClick={handleMarkDeleted}
              disabled={selectedIndices.length === 0}
              title="移除选中消息"
            >
              <Trash2 className="h-4 w-4" />
            </button>
          </div>
        </div>
      </div>
      
      {/* 消息列表 - 可滚动区域 */}
      <div className="flex-1 overflow-y-auto border-gray-200">
        {messages?.map(msg => {
          // 如果消息被标记为已删除且设置为隐藏已删除，则跳过
          if (deletedItems.has(msg.index) && !showDeleted) {
            return null;
          }
          
          // 如果消息被标记为已完成且设置为隐藏已完成，则跳过
          if (markedItems.has(msg.index) && !showCompleted) {
            return null;
          }
          
          // 分支信息
          let branchInfo = "";
          if (msg.is_branch_point) {
            branchInfo = "🔀";
          } else if (msg.branch_level > 0) {
            branchInfo = `${msg.branch_level}`;
          }
          
          // 标记信息
          let markInfo = null;
          
          if (deletedItems.has(msg.index)) {
            markInfo = <span className="deleted-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffcdd2' : '#f44336'}}>🗑️</span>;
          } else if (markedItems.has(msg.index) && importantItems.has(msg.index)) {
            markInfo = (
              <div className="dual-mark" style={{color: selectedIndices.includes(msg.index) ? 'white' : 'inherit'}}>
                <span className="completed-mark" style={{color: selectedIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>
                <span className="important-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>
              </div>
            );
          } else if (markedItems.has(msg.index)) {
            markInfo = <span className="completed-mark" style={{color: selectedIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>;
          } else if (importantItems.has(msg.index)) {
            markInfo = <span className="important-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>;
          }

          // 已删除消息的样式
          const isDeleted = deletedItems.has(msg.index);
          const deletedStyle = isDeleted ? { textDecoration: 'line-through', opacity: 0.6 } : {};

          const grid = "grid grid-cols-12 px-1 py-3 text-sm";
          const messagePreviewClass = selectedIndices.includes(msg.index) 
            ? "col-span-4 px-1 pl-3 truncate text-white" 
            : "col-span-4 px-1 pl-3 truncate text-gray-700";
          
          return (
            <div 
              key={msg.index} 
              className={`border-b border-gray-200 hover:bg-gray-100 ${
                msg.sender === 'human' ? 'human-msg' : 'claude-msg'
              } message-list-item cursor-pointer`}
              onClick={(e) => handleMessageClick(msg.index, e.ctrlKey)}
              style={{
                backgroundColor: selectedIndices.includes(msg.index) ? '#5c6bc0' : '',
                color: selectedIndices.includes(msg.index) ? 'white' : '',
                ...deletedStyle
              }}
            >
              <div className={grid}>
                <div className="col-span-1 px-1 text-center">{msg.index + 1}</div>
                <div className="col-span-2 px-1 font-medium">
                  {msg.sender_label}
                </div>
                <div className="col-span-3 px-1 text-xs" style={{color: selectedIndices.includes(msg.index) ? '#e0e0e0' : '#6b7280'}}>
                  {msg.timestamp}
                </div>
                <div className="col-span-1 px-1 text-center" style={{color: selectedIndices.includes(msg.index) ? '#e0e0e0' : '#9c27b0'}}>
                  {branchInfo}
                </div>
                <div className="col-span-1 px-1 text-center mark-container">
                  {markInfo}
                </div>
                <div className={messagePreviewClass}>{msg.display_text?.substring(0, 100)}</div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default MessageList;